<template>
  <CRow class="justify-content-center">
    <CCol>
      <CCard>
        <CCardHeader class="d-flex justify-content-between">
          <div>
            <slot name="header">
              <CIcon name="cil-healing" />
              <strong>Item List </strong>
            </slot>
          </div>
        </CCardHeader>
        <CCardBody>
          <v-client-table
            :data="rows"
            :columns="columns"
            :options="options"
          >
          </v-client-table>
        </CCardBody>
      </CCard>
    </CCol>
  </CRow>
</template>
<script>
export default {
  name: "Receipt",
  data() {
    return {
      rows: [
        {
          id: 1,
          drug_name: "Coraxil",
          unit_price: "200",
          quantity: "25",
          amount: "5000 "
        },
        { id: 2, name: "Jane", year: "6", number: "18" },
        { id: 3, name: "Susan", year: "7", number: "19" },
        { id: 4, name: "Chris", year: "8", number: "20" },
        { id: 5, name: "Dan", year: "9", number: "21" }
      ],
      columns: [
        "id",
        "drug_name",
        "unit_price",
        "quantity",
        "amount",
      ],
      options: {
        columnsClasses: {
          method: "text-center"
        },
        filterByColumn: true,
        texts: {
          filter: "Filter:",
          filterBy: "Filter by {column}",
          count: " "
        },
        filterable: ["drug_name"],
        headings: {
          name: "Name",
          id: "ID"
        }
      }
    };
  },
  methods: {
    click(e) {
      console.log(e);
    },
    goToBatches(row) {
      this.$router.push({ name: "Batches", params: { row: row } });
    }
  }
};
</script>